// A helper for defining breakpoints
@mixin breakpoint($point) {
    @if $point=='lg' {
        @media (min-width: 992px) {
            @content ;
        }
    }

    @else if $point=='md' {
        @media (min-width: 768px) {
            @content ;
        }
    }

    @else if $point=='sm' {
        @media (min-width: 576px) {
            @content ;
        }
    }
}

// Color variables
$black: #000000ff;
$blackTransparent: rgba(0, 0, 0, 0.4);
$yellow: #fee342ff;
$yellowTransparent: rgba(254, 226, 66, 0.4);
$yellowDark: rgb(94, 83, 23);
$blue: #16e1b6ff;
$blueTransparent: rgba(22, 225, 181, 0.4);
$blueDark: rgb(10, 97, 78);
$darkgrey: #454c4eff;
$lightgrey: rgb(225, 233, 235);
$almostBlack: rgb(36, 40, 41);

// Fonts
@import url('https://fonts.googleapis.com/css2?family=Nunito&family=Roboto+Condensed&display=swap');

// Common styles
html,
body,
#root {
    margin: 0;
    padding: 0;
    height: 100%;
    background-color: $black !important;
    color: $lightgrey;
}

.form-check-input {
    &:checked {
        background-color: $yellow !important;
        border-color: $yellow !important;
    }

    &:focus {
        border-color: $yellow !important;
        box-shadow: 0 0 3px 3px $yellowDark !important;
    }

    &:checked[type="checkbox"] {
        background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 20 20'%3e%3cpath fill='none' stroke='%23000' stroke-linecap='round' stroke-linejoin='round' stroke-width='3' d='M6 10l3 3l6-6'/%3e%3c/svg%3e") !important;
    }
}

.container {
    font-family: 'Nunito', sans-serif;
}

p strong {
    color: $yellow;
}

.btn {
    text-transform: uppercase;
    font-family: 'Roboto Condensed', sans-serif;
    border: 3px;
}

.btn-danger {
    background-color: $black !important;
    color: $blue !important;
    border-color: $blue !important;

    &:hover {
        background-color: $blueDark !important;
    }

    &:focus {
        box-shadow: 0 0 0 1px $blue !important;
    }
}

.btn-primary {
    background-color: $black !important;
    border-color: $yellow !important;
    color: $yellow !important;

    &:hover {
        background-color: $yellowDark !important;
    }

    &:focus {
        box-shadow: 0 0 0 1px $yellow !important;
    }

    // clip-path: polygon(10px 0, 100% 0, 100% 100%, 0 100%, 0 10px);
}

.form-control,
.form-select {
    background-color: $darkgrey !important;
    color: white !important;

    &:focus {
        border-color: $blue !important;
        box-shadow: 0 0 3px 3px $blueDark !important;
    }
}

#errorMessage {
    z-index: 2;
}

#notifToast {
    z-index: 2;
    bottom: 10px;
    right: 10px;
    background-color: black;
    border: 1px solid $yellow;

    .toast-header {
        background-color: $yellow;
        color: black;

        svg {
            margin: 0 5px 0 0;
        }
    }
}

.toast:not(.showing):not(.show) {
    display: none;
}

.install-button {
    width: 50px;
    height: 50px;
    border-radius: 50px;
    background-color: $yellow;
    z-index: 4;
    bottom: 10px;
    right: 10px;
    cursor: pointer;

    svg {
        height: 26px;
        width: 26px;
        color: black;
        margin: 12px;
    }

    @include breakpoint(lg) {
        display: none;
    }
}

.loading {
    z-index: 3;
    color: $yellow;
    width: 5em;
    height: 5em;
}

h1,
h2,
h3 {
    font-family: 'Roboto Condensed', sans-serif;
    color: $blue;
}

.text-container {
    padding: 1em;
    color: $lightgrey;
    position: relative;
    font-size: 1.15em;

    @include breakpoint(sm) {
        text-align: justify;
    }
}

