.navbar-toggler {
    background-color: $yellow !important;

    &:focus {
        border: 1px solid white;
    }
}

.navbar {
    font-family: 'Roboto Condensed', sans-serif;

    a,
    a:hover,
    a:visited {
        color: $yellow;
        text-transform: uppercase;
        text-shadow: 0 0 10px $black;
        text-decoration: none;
        font-size: 1.3em;
    }

    a:hover {
        text-decoration: underline;
    }

    .form-control:focus {
        border-color: $yellow !important;
        box-shadow: 0 0 3px 3px $yellowDark !important;
    }

}

.navbar-brand {
    font-size: 3em !important;
    height: 85px;
}

.nav-item {
    display: flex;
    align-items: center;
    padding: 0 20px;
    min-height: 50px;
    color: $yellow;
    text-transform: uppercase;

    a:focus {
        text-shadow: 0 0 5px $yellow;
    }
}

.navbar-collapse {
    background-color: $black;
    z-index: 1;
    width: 100vw;
    padding-bottom: 10px;
    border: 1px solid $yellow;
    border-top: none;
    border-radius: 0 0 7px 7px;

    @include breakpoint(lg) {
        padding-bottom: 0;
        background-color: transparent;
        border: none;
    }
}


// Dashboard
.table-operations {
    display: block;

    @include breakpoint(md) {
        display: flex;
    }
}

.text-container .nav-item:hover {
    color: $yellow;
    text-shadow: 0 0 3px $yellow;
}

.table-dark th,
.table-dark td {
    background-color: $black !important;
}

// Chat
.my-message {
    margin: auto 0 1rem auto;
}

.chat-container {
    position: absolute; // A trick to make div occupy the whole space
    top: 107px;
    bottom: 0;
    right: 0;
    left: 0;

    .toast {
        background-color: $darkgrey;
        font-size: 0.8em;
        border: 1px solid $blue;
    }

    .toast-header {
        background-color: $almostBlack;
        color: $blue;
    }
}

.main-nav {
    height: 10vh;
}

.no-decoration {
    text-decoration: none;
}

.chat-list-item {
    background-color: $darkgrey !important;
    margin: 0.7rem 0;
    padding: 0.7rem 1.2rem !important;
    border-radius: 3px;
    color: $lightgrey !important;
    text-decoration: none;
    border-radius: 20px !important;

    &:hover {
        background-color: $almostBlack !important;
        cursor: pointer;
        border: 1px solid $blue !important;
    }
}

.chat-list {
    margin: 1em 0;
}

.messages-container {
    margin-bottom: 0.5em;
}

//Player pages
.player-controls {
    height: 80vh;
    justify-content: space-around;
    flex-direction: column;

    a {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    @include breakpoint(md) {
        height: auto;
        flex-direction: row;

        a {
            display: inline-block;
        }
    }
}

//Character editor
.pell-content {
    height: 200px;
    background-color: $darkgrey;
    color: white;
}

.pell-actionbar {
    background-color: $black;
    border: 1px solid $darkgrey;
}

.pell-button {
    color: $blue;

    &:hover {
        background-color: $darkgrey;
    }

}

.pell-button-selected {
    background-color: $blue;
    color: black;
}



//Front page
.front-image {
    height: 60vh;
    max-height: 500px;
    max-width: 2000px;
    margin: auto;
    background-image: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0) 70%, black), url('../images/front-small.jpeg');
    background-size: cover;

    @include breakpoint(md) {
        background-image: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0) 70%, black), url('../images/front.jpeg');
    }
}

.front-container {

    padding-top: 0;
}

// Transactions
.transactions-table {
    color: $lightgrey !important;
}

// Info page
.info-box {
    float: right;
    padding: 1em;
    border: 1px solid $blue;
    box-shadow: 0 0 15px $blue;
    margin: 0 0 2em 2em;
    background-color: $darkgrey;
    color: white;

    ul {
        list-style: none;
        /* Remove default bullets */
    }

    ul li::before {
        content: "\25C8";
        display: inline-block;
        /* Needed to add space between the bullet and the text */
        width: 1.4em;
        margin-left: -1em;
    }
}